import React from 'react'
import Link from 'gatsby-link'

const NotFoundPage = () => (
  <div>
    <h1>404</h1>
    <p>Sorry, this page doesn't exist! Double check your URL or go the <Link to="/">homepage</Link></p>
  </div>
)

export default NotFoundPage



